<template>
  <div class="layout">
    <Menu mode="horizontal" :active-name="$route.name" @on-select="go">
      <div class="layout-logo">
        <img style="width: 40px;" src="../assets/image/logo.png" alt="">
      </div>
      <div class="layout-assistant">
        <Menu-item name="project">
          <Icon type="ios-home"></Icon>
          我的项目
        </Menu-item>
        <Menu-item name="workbench">
          <Icon type="ios-heart"></Icon>
          </Icon>工作台
        </Menu-item>
        <Menu-item name="team">
          <Icon type="person-stalker"></Icon>
          我的团队
        </Menu-item>
        <Menu-item name="doc">
          <Icon type="document-text"></Icon>
          文档
        </Menu-item>
        <Menu-item name="lang">
          <Icon type="code"></Icon>
          语法
        </Menu-item>
        <Submenu name="user" style="float: right;" v-if="user">
          <template slot="title">
            <img :src="baseUrl + user.imgUrl" style="width: 30px; height: 30px; border-radius: 15px; position: relative; top: 7px;">
            {{user.nickName}}
          </template>
          <Menu-item name="user">
            <Icon type="ios-gear"></Icon>
            编辑资料
          </Menu-item>
          <Menu-item name="exit">
            <Icon type="android-exit"></Icon>
            退出
          </Menu-item>
        </Submenu>
      </div>
    </Menu>
    <div class="layout-content">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
  import { getUser, setNew } from '../config/data'
  import getData from '../config/getData'
  import baseUrl from '../config/baseUrl'

  export default {
    created () {
      getUser().then(user => {
        this.user = user
      })
    },
    data () {
      return {
        baseUrl,
        user: null
      }
    },
    methods: {
      go (name) {
        if (name === 'exit') {
          getData('loginOut').then(() => {
            setNew()
            this.$router.push({
              path: '/index'
            })
          })
        } else {
          this.$router.push({
            path: '/app/' + name
          })
        }
      }
    }
  }
</script>
<style scoped type="text/scss" lang="scss">
  .layout {

    .layout-assistant {
      height: inherit;
    }

    .layout-content {
      min-height: 200px;
      margin: 15px;
      overflow: hidden;
      background: #fff;
      border-radius: 4px;
    }

    .layout-logo {
      width: 80px;
      height: 60px;
      border-radius: 3px;
      float: left;
      position: relative;
      padding: 10px 20px;
    }
  }

</style>



